import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, hasWritePrivilege } from "../../../stores/Utils";

import "./dashboard.css"
import {  UpdProvider_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { getall_provider, sortSubProviders, delete_provider } from "../services/server_fetching";
import { SvgIcon_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";
import { useNavigate } from "react-router";
import { getall_collects, getall_toPay, getSum_paycollect } from "../../Cobros/services/server_fetching";
import { CollectViewM_Cmpt, PayManualM_Cmpt } from "../../Cobros";

const Svg = SvgIcon_Cmpt();

const UpdProvider = UpdProvider_Cmpt();

const LoadingColorSpinner = LoadingColorSpinner_Cmpt();


const CollectViewM = CollectViewM_Cmpt();
const PayManualM = PayManualM_Cmpt();





const ProviderItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loadingAmount, set_loadingAmount] = useState(0);

	const navigate = useNavigate();
	let idKey = data.account + "_"+ data.subAccount;
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	
	const refreshAccs =  () => {
		getall_provider(updObs)
	}

	const update = async () => {
		if(hasWritePrivilege()){
			updStore("upd_provider", data);
			updStore("upd_provider_bck", JSON.parse(JSON.stringify(data)));
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true };
			modaldata["content"] = <UpdProvider confirm={refreshAccs} />;
			OpenModal(modaldata);
		}
	}

	const openCollectManual =  () => {
		// if(hasWritePrivilege()){}
			updStore("upd_provider", data);
			updStore("upd_provider_bck", JSON.parse(JSON.stringify(data)));
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true };
			modaldata["content"] = <CollectViewM confirm={refreshAccs} />;
			OpenModal(modaldata);
		
	}

	const openPaytManual =  () => {
		// if(hasWritePrivilege()){}
			updStore("upd_provider", data);
			updStore("upd_provider_bck", JSON.parse(JSON.stringify(data)));
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true };
			modaldata["content"] = <PayManualM confirm={refreshAccs} />;
			OpenModal(modaldata);
		
	}


	
	
	
	const openSubMayor =  (sba) => {
		if(data?.type==="provider"){
			let nn= {
				"account":data?.account,
				"subAccount":data?.subAccount,
				customerId:data?.customerId
			};
			updStore("Acc2Pay", nn);
			getall_toPay(nn,()=>{
				openPaytManual();
			});
			

		}
		else if(data?.type==="customer"){
			let nn= {
				"account":data?.account,
				"subAccount":data?.subAccount,
				customerId: data?.customerId
			};
			updStore("Acc2Collect", nn);
			getall_collects(nn, ()=>{

				openCollectManual();
				/* 
				navigate({
					pathname: "/collect" ,
					//search: `?cId=${element.comprobanteId}`
				});
				*/
			});
		}
	}



	const openHistory =  () => {
		if(data?.type==="provider"){
			let nn= {
				"account":data?.account,
				"subAccount":data?.subAccount,
				customerId:data?.customerId
			};
			updStore("Acc2Pay", nn);
			getall_toPay(nn,()=>{
				navigate({
					pathname: "/pay" ,
					//search: `?cId=${element.comprobanteId}`
				});
			});
		}
		else if(data?.type==="customer"){
			let nn= {
				"account":data?.account,
				"subAccount":data?.subAccount,
				customerId: data?.customerId
			};
			updStore("Acc2Collect", nn);
			getall_collects(nn, ()=>{
				navigate({
					pathname: "/collect" ,
					//search: `?cId=${element.comprobanteId}`
				});
			});
		}
	}



	const deleteComprobante =  () => {
		delete_provider(data?.providerId, handleRefreshAll);
	}
	

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data} />
			OpenModal(modaldata);
		}
	}

	

	let subAcc = sortSubProviders( getStore("sub_provider_group")?.[data.providerId],"subProvider");
	
	



	
	const recalcTotalize =  (sba) => {
		getSum_paycollect(data,idKey,()=>{
			setTimeout(() => {
				set_loadingAmount(false);
				updObs();
			}, 200);
			
		})
	}




	const handleHover =  (sba) => {
		
		if(!getStore("rec_by_acc")?.[idKey] ){
			set_loadingAmount(true)
			recalcTotalize(updObs);
		};
	}

	let hasToPay  =  (data?.type === "provider" &&  getStore("rec_by_acc")?.[idKey] && !isNaN(getStore("rec_by_acc")?.[idKey]*-1) )
	let hasToCollect  = (data?.type === "customer" &&  getStore("rec_by_acc")?.[idKey] && !isNaN(getStore("rec_by_acc")?.[idKey]*1));
					


	return (
		<div className="item_acc" id={`${idKey}`}>
			<div className="_dsplFlx item_box provider_header" onMouseEnter={handleHover}>
				<div className="_dsplFlx"  onClick={update}>
					<div className="code" >
						
					</div>
					<div className="name">
						{data?. name}
					</div>
					<div className="unit" >
						{data?.unit} 
					</div>
				</div>
				<div className="flexSpace"/>
				<div className=" addSbAcc purple" onClick={()=>openHistory(null)}>
					{"Historial"}
				</div>
				
				{loadingAmount?
				<div className="_dsplFlx" >
					<div className="icon" style={{marginRight: 19}}>
						<LoadingColorSpinner stroke={"#646cff"} size={24}/>
					</div>
					<div className="loading_lbl">
						cargando ....
					</div>
				</div>
				:
				<div>
					{hasToCollect || hasToPay? 
					<div className=" addSbAcc purple" onClick={()=>openSubMayor(null)}>
						{hasToPay?  `Por Pagar $${(getStore("rec_by_acc")?.[idKey]*-1).toFixed(2)}`: hasToCollect?`Por Cobrar $${(getStore("rec_by_acc")?.[idKey]*1).toFixed(2)}`:null}
					</div>
					:null}
				</div>
				
				}
				{hasWritePrivilege()?
					<div className="icon" style={{marginLeft: 19}} onClick={OpenDeleteAlert}>
						<Svg name={"outline_delete"} color={"#c62828"}/>
					</div>
				:null}
			</div>
			
		</div>
	)
}
export default ProviderItem;